.logo {
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 26px;
}


.nav-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 65%;
  margin: auto;
  height: 2rem
}

.navlink {
  text-decoration: none;
  font-size: 17px;
  color: black;
}
.logo-link {
  text-decoration: none;
  color: black;
}
.home-container {
  margin: auto;
  display: block;
}

.home {
  margin: auto;
  display: block;
}

h1 {
  display: block;
  width: 500px;
  margin: 4rem auto;
}
.profile-wrapper {
  text-align: center;
}

