.textarea {
  font-size: 14px;
  padding-left: 5px;
}
.save-delete {
  margin: 20px 0 10px 450px;
}

.delete-list {
  color: red;
  margin: 5px;
  padding: 5px 5px;
  background-color: transparent;
}

.save-list {
  color: green;
  margin: 5px;
  padding: 5px 5px;
  background-color: transparent;
} 
.list-container {
  margin: 15px 0 20px 0;
}

.movie-item {
  display: grid;
  /* position:  */
  grid-template-columns: 1fr 5fr 20fr 5fr;
  font-size: 15px;
  border: black solid 1px;
  margin: auto;
  width: 42%; 
}

.movie-link {
  text-decoration: none;
  color: inherit;
}

.grip-container {
  padding: 0rem .5rem;
}

.grip {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: .1rem;
  width: .8rem;
  cursor: pointer;
  /* white-space: nowrap;
  overflow: hidden;
  overflow: hidden;
  outline: none; */
}

.movie-info {
  margin: 5px 0;
}

.dir-year {
  margin-top: 2px;
}

.sortableHelper {
  text-align: center;
  right: 0px;
}

.delete {
  white-space: nowrap;
  overflow: hidden;
  font-size: 18px;
  background-color: Transparent;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}

.numbers {
  white-space: nowrap;
  overflow: hidden;
  color: grey;
  font-style: bolder;
  font-size: 26px;
  margin: auto;
  margin-left: 13px;
}
/* Search Results */
.result-item {
  font-size: 15px;
  height: 25px;
  border-bottom: lightgrey solid 1px;
  margin: auto;
  width: 42%;
}

.result-info {
  margin: 5px 0;
}

.result-year-director {
  margin-top: 2px;
}



